import React from "react";
import { Menu } from "antd";
import "./index.css";

type Props = {
  onClick: (key: { key: string }) => void;
};

const items = [
  { key: "player", label: "" },
  { key: "pairing", label: "" },
  { key: "match", label: "" },
];

const FooterMenu: React.FC<Props> = (props) => {
  const { onClick } = props;

  return (
    <Menu
      theme="dark"
      mode="horizontal"
      defaultSelectedKeys={["player"]}
      items={items}
      onClick={onClick}
      className="footer-menu"
    />
  );
};

export default FooterMenu;
